@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
/* Google Fonts import */

/* Color schemes from colors.co */
:root {
  /* Light-mode */
  --color1: #bcb6ffff;
  --color2: #b8e1ffff;
  --color3: #a9fff7ff;
  --color4: #94fbabff;
  --color5: #82aba1ff;
  /* Dark-mode */
  --color6: #0a0908ff;
  --color7: #22333bff;
  --color8: #f2f4f3ff;
  --color9: #a9927dff;
  --color10: #5e503fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Audiowide', cursive;
  text-align: center;
}

h1 {
  padding: 1rem 0;
}

h2 {
  padding-bottom: 1rem;
}

input, select {
  display: block;
  margin: 1rem auto;
}

#form,
#displayBox,
#displayBox2,
#displayBox3 {
  width: 80%;
  max-width: 700px;
  padding: 1rem;
  margin: 1rem auto;
  border: 4px solid #94fbabff;
  border: 4px solid var(--color4);
  border-radius: 10px;
  background-color: #bcb6ffff;
  background-color: var(--color1);
  box-shadow: 10px 10px 5px #82aba1ff;
  box-shadow: 10px 10px 5px var(--color5);
}

#submitButton,
#calculateButton,
#resetButton,
#switchThemeButton {
  padding: 6px 16px;
  border: 2px solid #82aba1ff;
  border: 2px solid var(--color5);
  background-color: #a9fff7ff;
  background-color: var(--color3);
  border-radius: 10px;
}

#submitButton:hover,
#submitButton:focus,
#calculateButton:hover,
#calculateButton:focus,
#resetButton:hover,
#resetButton:focus,
#switchThemeButton:hover,
#switchThemeButton:focus {
  cursor: pointer;
  box-shadow: 10px 10px 5px #82aba1ff;
  box-shadow: 10px 10px 5px var(--color5);
}

#result {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  min-height: 1.6rem;
  padding: 0.4rem;
  border: 2px solid #0a0908ff;
  border: 2px solid var(--color6);

  font-size: 2rem;
  font-weight: 900;
}



/* Color schemes from colors.co */
:root {
    /* Light-mode */
    --color1: #bcb6ffff;
    --color2: #b8e1ffff;
    --color3: #a9fff7ff;
    --color4: #94fbabff;
    --color5: #82aba1ff;
    /* Dark-mode */
    --color6: #0a0908ff;
    --color7: #22333bff;
    --color8: #f2f4f3ff;
    --color9: #a9927dff;
    --color10: #5e503fff;
}

.light-box {
    border: 4px solid #94fbabff !important;
    border: 4px solid var(--color4) !important;
    background-color: #bcb6ffff !important;
    background-color: var(--color1) !important;
    box-shadow: 10px 10px 5px #82aba1ff !important;
    box-shadow: 10px 10px 5px var(--color5) !important;
}

.dark-box {
    border: 4px solid #0a0908ff !important;
    border: 4px solid var(--color6) !important;
    background-color: #22333bff !important;
    background-color: var(--color7) !important;
    color: #f2f4f3ff !important;
    color: var(--color8) !important;
    box-shadow: 10px 10px 5px #5e503fff !important;
    box-shadow: 10px 10px 5px var(--color10) !important;
}

.light-button {
    border: 2px solid #82aba1ff;
    border: 2px solid var(--color5);
    background-color: #a9fff7ff;
    background-color: var(--color3);
}

.dark-button {
    border: 2px solid #5e503fff;
    border: 2px solid var(--color10);
    background-color: #f2f4f3ff;
    background-color: var(--color8);
}

.light-button:hover,
.light-button:focus {
    box-shadow: 10px 10px 5px #82aba1ff;
    box-shadow: 10px 10px 5px var(--color5);
}

.dark-button:hover,
.dark-button:focus {
    box-shadow: 10px 10px 5px #5e503fff;
    box-shadow: 10px 10px 5px var(--color10);
}
