/* Google Fonts import */
@import url('https://fonts.googleapis.com/css2?family=Audiowide&display=swap');

/* Color schemes from colors.co */
:root {
  /* Light-mode */
  --color1: #bcb6ffff;
  --color2: #b8e1ffff;
  --color3: #a9fff7ff;
  --color4: #94fbabff;
  --color5: #82aba1ff;
  /* Dark-mode */
  --color6: #0a0908ff;
  --color7: #22333bff;
  --color8: #f2f4f3ff;
  --color9: #a9927dff;
  --color10: #5e503fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Audiowide', cursive;
  text-align: center;
}

h1 {
  padding: 1rem 0;
}

h2 {
  padding-bottom: 1rem;
}

input, select {
  display: block;
  margin: 1rem auto;
}

#form,
#displayBox,
#displayBox2,
#displayBox3 {
  width: 80%;
  max-width: 700px;
  padding: 1rem;
  margin: 1rem auto;
  border: 4px solid var(--color4);
  border-radius: 10px;
  background-color: var(--color1);
  box-shadow: 10px 10px 5px var(--color5);
}

#submitButton,
#calculateButton,
#resetButton,
#switchThemeButton {
  padding: 6px 16px;
  border: 2px solid var(--color5);
  background-color: var(--color3);
  border-radius: 10px;
}

#submitButton:hover,
#submitButton:focus,
#calculateButton:hover,
#calculateButton:focus,
#resetButton:hover,
#resetButton:focus,
#switchThemeButton:hover,
#switchThemeButton:focus {
  cursor: pointer;
  box-shadow: 10px 10px 5px var(--color5);
}

#result {
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  min-height: 1.6rem;
  padding: 0.4rem;
  border: 2px solid var(--color6);

  font-size: 2rem;
  font-weight: 900;
}


